
import conf from '../conf'
const NAMESPACE = conf.NAMESPACE
const CAN_USER_PV_LIMIT = conf.CAN_USER_PV_LIMIT

const _build = function(ul, id, id_field, layer = -1) {
    let res = []

    layer += 1

    ul.map(e => {
        if (e[id_field] == id) {

            const children = _build(ul, e[
                NAMESPACE.unid
            ], id_field, layer)

            if (children && children.length > 0) {
                e[
                    NAMESPACE.children
                ] = children
            }

            e[
                NAMESPACE.index
            ] = layer
            
            res.push(e)
        }
    });

    return res
}

const build_user_relation = function(ul, model = "SPONSER") {
    let res = ''
    if (model == 'SPONSER') {
        res = _build(ul, '', 
            NAMESPACE.sponser_id
        )
    } else {
        res = _build(ul, '', 
            NAMESPACE.enroller_id
        )
    }

    return res
}

export default build_user_relation


