<template>
    
    <div>

        <v-card>
            <v-row class="pa-4 px-6">
                <v-col cols="6" class="mb-0 pb-0">
                    <user-model-select @getUser_Father="getUser" :def="'s'"></user-model-select>
                </v-col>
                <v-col cols="6" class="mb-0 pb-0 text-right">
                    <v-btn @click="numResult" class="btn-purple">计算</v-btn>
                </v-col>
            </v-row>
        </v-card>

        <v-card class="">
            <v-row class="pa-4 px-6">

                <v-col class="pa-0">
                    <alb-table-header></alb-table-header>
                    <div class="mt-4"></div>
                </v-col>
                <v-col cols="12" class="pa-0" v-if="show">

                        <!-- 子类地方  -->

                        <v-treeview
                            :items="users" open-all :open="[0, 1]"
                            class="mt-0 the-tree fs-14"
                            multiple-active transition dense off-icon
                            v-if="show"
                        >
                            <template v-slot:label="{ item, open }">
                                <alb-tree-label :the_item="item" :op="open"></alb-tree-label>
                            </template>
                            <template v-slot:append="{ item, open }">
                                <alb-tree-item ref="treeItem" :allow="numing" :the_item="item" :o="open"></alb-tree-item>
                            </template>
                        </v-treeview>

                        <!-- 子类结束 -->

                </v-col>
                <v-col cols="12" class="pt-0" v-else>

                    计算中...

                </v-col>

            </v-row>
        </v-card>
    </div>

</template>

<script>

import rolling_api from '@/common/es/bonus/rolling_api'

import user_data from '@/apis/mock/user_unit_data'
import user_relation from '@/common/es/bonus/userApi/user_relation'

import AlbTreeItem from './AlbTreeItem.vue'
import UserModelSelect from '@/components/Form/UserModelSelect.vue'
import AvatarAndName from '@/components/User/AvatarAndName.vue'
import AlbTableHeader from './AlbTableHeader.vue'
import AlbTreeLabel from './AlbTreeLabel.vue'
    export default {
  components: { AlbTreeItem, UserModelSelect, AvatarAndName, AlbTableHeader, AlbTreeLabel },
        name: '',
        data() {
            return {
                users: [ ],
                show: false,
                numing: false
            }
        },
        mounted() {
            this.getUser('s')
            this.reloadTree(true)

        },
        methods: {
            reloadTree(f) {
                this.show = f
            },
            numResult() {
                this.reloadTree(false)

                this.numing = true
                
                setTimeout(() => {
                    this.reloadTree(true)
                }, 1000)
                
            },  
            getUser(code) {
                
                this.reloadTree(false)
                
                if (code == 's') { 
                    this.users = user_relation(user_data, 'SPONSER')
                } else {
                    this.users = user_relation(user_data, 'ENROLLER')
                }

                let ul = Array.from(this.users)
                rolling_api(ul)

                setTimeout(() => {
                    this.reloadTree(true)
                }, 1000)
            }
        }
    }
</script>

<style lang="sass" scoped>
        
</style>


